import moment from 'moment'
export default {
  data: () => ({
    book: null,
    user: null,
    endDate: null,
    error: null,
    userForm: true,
  }),

  watch: {
    book(newVal) {
      this.userForm = !newVal
    },

    user() {
      this.getEndDate()
    },
  },
  computed: {
    books() {
      const books = this.$store.state.book.books.map((book) => {
        book.title = `${book.title} (${book._id})`
        return book
      })
      return books
    },
    users() {
      return this.$store.state.user.users.map((user) => {
        user.name = `${user.first_name} ${user.last_name}`
        return user
      })
    },

    endDateStr: {
      get() {
        return this.endDate ? moment(this.endDate).format('YYYY-MM-DD') : null
      },
      set(date) {
        this.endDate = new Date(date)
      },
    },
  },
  async created() {
    await this.$store.dispatch('book/fetchAvaliableBooks')
    await this.$store.dispatch('user/fetchUsers')
  },

  methods: {
    async borrow() {
      const { book, user } = this

      const form = { book, user, endDate: this.endDate }

      await this.$store
        .dispatch('transaction/borrow', form)
        .then(() => {
          return this.$router.push('/history-transaction')
        })
        .catch((e) => {
          this.error = e
        })
    },

    async getEndDate() {
      const { job } = await this.$store.dispatch('user/fetchUser', this.user)
      const { book_type: type } = await this.$store.dispatch(
        'book/fetchBook',
        this.book
      )

      this.endDate = await this.$store.dispatch('transaction/endDate', {
        job,
        type,
      })
    },
  },
}
